<template>
    <div class="home container-xxl text-center">
      <h1>Movie Lair Official Discord Server</h1>
      <br>
      <a href="https://discord.gg/ZvRTYqTWtZ" target="_blank" class="btn btn-primary">Join Now</a>
      <br>
    </div>
  </template>

<script lang="ts">
import { defineComponent } from 'vue'
import { time } from 'vue-gtag'

export default defineComponent({
  name: 'DiscordView',
  data () {
    return {
      genres: null,
      shows: {}
    }
  },
  created () {
    this.getShowGenres()
  },
  mounted () {
    this.timeTrack()
  },
  methods: {
    getShowGenres () {
      document.title = 'Movie Lair Official Discord Server Contact - MovieLair'
    },
    timeTrack () {
      // Feature detects Navigation Timing API support.
      if (window.performance) {
        // Gets the number of milliseconds since page load
        // (and rounds the result since the value must be an integer).
        const timeSincePageLoad = Math.round(performance.now())

        // send timing event
        time({
          name: 'discord_page_load',
          value: timeSincePageLoad,
          event_category: 'Overall Application Performance'
        })
      }
    }
  }
})

</script>

  <style scoped lang="scss">
  </style>
